import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
// import DropdownButton from "react-bootstrap/DropdownButton";
// import Dropdown from "react-bootstrap/Dropdown";
import Table from "react-bootstrap/Table";
import Pagination from "../components/Pagination";
import Spinner from "react-bootstrap/Spinner";
// import { FaBinoculars } from "react-icons/fa";
// import { FaRegEdit } from "react-icons/fa";
import { useHistory } from "react-router-dom";

const ListApplications = () => {
  const [applications, setApplication] = useState([]);
  const [q, setQ] = useState("");

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadApplications();
  }, []);

  let axiosConfig = {
    headers: {
      "Content-Type": "application/json;charset=UTF-8",
      "Access-Control-Allow-Origin": "http://localhost:3000",
    },
  };

  const loadApplications = async () => {
    setLoading(true);
    console.log("loading data....");

    const result = await axios.get(
      "https://testspace03.azurewebsites.net/api/application",
      axiosConfig
    );
    const input = result.data;
    setApplication(input);
    setLoading(false);
  };

  function search(rows) {
    return rows.filter(
      (row) =>
        JSON.parse(row.json).name.toLowerCase().indexOf(q) > -1 ||
        JSON.parse(row.json).filecode.toLowerCase().indexOf(q) > -1
    );
  }

  const [showPerPage, setShowPerPage] = useState(20);
  const [pagination, setPagination] = useState({
    start: 0,
    end: showPerPage,
  });

  // Change Page
  const onPaginationChange = (start, end) => {
    setPagination({ start: start, end: end });
  };

  if (loading) {
    return (
      <div class="container">
        <Spinner animation="border" />
      </div>
    );
  }
  return (
    <div class="container">

        <div>
          <div class="row">
            <div class="col-xs-4 col-md-4 col-sm-4">
              {" "}
              <Link className="btn btn-primary btn-sm" to="/">
                back to Home
              </Link>
            </div>
            <div class="col-xs-4 col-md-4 col-sm-4">
              <h5>Applications List</h5>
            </div>
            <div class="col-xs-4 col-md-4 col-sm-4">
              {" "}
              <div class="row py-2">
                <div class="col-2">
                  <input
                    type="text"
                    placeholder="Search list for"
                    value={q}
                    onChange={(e) => setQ(e.target.value)}
                  ></input>
                </div>
              </div>
            </div>
          </div>
        </div>


      <div class="container2">
        <div className="w-95 mx-auto shadow p-2">
          <div class="container3">
            <Table responsive="sm" striped bordered hover>
              <tbody>
                <th>ID</th>
                <th>File Code</th>
                <th>Applicant</th>
                <th>Deatils</th>
                <th></th>

                {search(applications)
                  .slice(pagination.start, pagination.end)
                  .map((application, index) => (
                    <tr key={application.id}>
                      <th scope="row" width="5%">
                        {application.id}
                      </th>
                      <td width="10%">
                        {JSON.parse(application.json).filecode +
                          " " +
                          JSON.parse(application.json).status}
                      </td>
                      <td width="20%">
                        {JSON.parse(application.json).name +
                          " " +
                          JSON.parse(application.json).phone +
                          " ₹" +
                          JSON.parse(application.json).amount}
                      </td>

                      {/* <td>{JSON.parse(application.json).amount}</td> */}
                      {/* <td>{JSON.parse(application.json).address}</td> */}
                      <td width="55%">
                        {JSON.parse(application.json).comment}
                      </td>

                      <td width="5%" align="center">
                        {/* <Link class="btn btn-outline-primary btn-sm mt-2 mr-4" to={`/donors/edit/${donor.id}`}><FaRegEdit></FaRegEdit></Link>
                        <Link class="btn btn-outline-primary btn-sm mt-2 mr-4" to={`/donors/edit/${donor.id}`}><FaBinoculars></FaBinoculars></Link> */}
                        <Link
                          class="mt-1 mr-2"
                          to={`/applications/edit/${application.id}`}
                        >
                          Edit
                        </Link>
                        <Link
                          class="mt-1 mr-2"
                          to={`/applications/view/${application.id}`}
                        >
                          View
                        </Link>
                      </td>
                    </tr>
                  ))}
              </tbody>
            </Table>
          </div>
        </div>
      </div>
      <Pagination
        showPerPage={showPerPage}
        onPaginationChange={onPaginationChange}
        total={search(applications).length}
        numberOfButtons={Math.ceil(search(applications).length / showPerPage)}
      />
    </div>
  );
};

export default ListApplications;
