import React, { useState } from "react";
import axios from "axios";
import { Link, useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";

const AddApplication = () => {
  let history = useHistory();
  const [category, setCategory] = useState();
  const { register, handleSubmit, errors } = useForm();

  const options = [
    { value: "medical", label: "Medicaldd" },
    { value: "education", label: "Education" },
    { value: "repair", label: "Repair & Maintence" },
    { value: "other", label: "Other" },
  ];

  const onSubmit = async (data, e) => {
    e.preventDefault();
    console.log(data);

    data.json = JSON.stringify(data);
    data.created = new Date();
    const headers = { "Content-Type": "application/json" };
    console.log(data);

    await axios.post(
      "https://testspace03.azurewebsites.net/api/application",
      JSON.stringify(data),
      {
        headers: headers,
      }
    );
    // } catch(err) {
    //   alert("sdsdsd");
    // }
    history.push("/");
  };

  return (
    <div className="container py-2">
      <div>
        <div class="row">
          <div class="col-xs-4 col-md-4 col-sm-4">
            <Link className="btn btn-primary btn-sm" to="/">
              Home
            </Link>
          </div>
          <div class="col-xs-4 col-md-4 col-sm-4">
            <h6>Add Application</h6>
          </div>
          <div class="col-xs-4 col-md-4 col-sm-4">
            <div class="row py-2">
              <div class="col-2"></div>
            </div>
          </div>
        </div>
      </div>

      <div className="mx-auto shadow p-2">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Filecode + Status
              </span>
            </div>
            <input
              type="text"
              name="filecode"
              placeholder="File code"
              class="form-control"
              ref={register({ required: true, maxLength: 12 })}
            />

            <input
              ref={register({ required: true })}
              type="text"
              name="status"
              placeholder="Status"
              class="form-control"
            />
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Name + Age
              </span>
            </div>
            <input
              type="text"
              name="name"
              placeholder="Name"
              class="form-control"
              ref={register({ required: true, maxLength: 50, minLength: 5 })}
            />

            <input
              ref={register({ min: 18, max: 99 })}
              type="number"
              name="age"
              placeholder="Age"
            />
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Address
              </span>
            </div>

            <input
              type="text"
              name="address"
              placeholder=""
              class="form-control"
              ref={register({ required: true, maxLength: 100, minLength: 5 })}
            />
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Phone + Email
              </span>
            </div>

            <input
              ref={register}
              type="tel"
              name="phone"
              placeholder="Phone"
              class="form-control"
              ref={register({ required: false, maxLength: 11, minLength: 8 })}
            />

            <input
              ref={register}
              type="text"
              name="email"
              placeholder="Email"
              class="form-control"
              ref={register({
                required: false,
                pattern: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
              })}
            />
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Category + Amount
              </span>
            </div>

            <select
              ref={register({ required: true })}
              name="category"
              class="form-control"
              placeholder="Category"
            >
              <option value="Medical">Medical</option>
              <option value="Education">Education</option>
              <option value="House Repair">House Repair</option>
              <option value="Other">Other</option>
            </select>

            <input
              ref={register({ required: false, maxLength: 6, minLength: 2 })}
              type="number"
              name="amount"
              placeholder="Amount"
            />
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Details/Actions
              </span>
            </div>
            <textarea
              ref={register}
              type="text"
              name="comment"
              placeholder=""
              rows="5"
              columns="130"
              class="form-control"
            />
          </div>

          <button className="btn btn-primary btn-block">Add Application</button>
        </form>
      </div>
    </div>
  );
};

export default AddApplication;
